:global(#painting_canvas) {
	position: relative;
	width: 100%;
	// max-width: 1366px;
	// height: 100%;
	:global(#main_layer) {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: auto;
	}

	:global(#painting_layer) {
		position: relative;
		top: 0;
		left: 0;
		width: 100%;
		height: auto;
	}

	:global(.active_layer) {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: auto;
	}
}

:global(#tool_menu) {
	padding: 20px;
	> *:not(:first-child) {
		margin-left: 20px;
	}
}

:global(.colorful) {
	position: relative;
	display: inline-block;

	:global(.preview_colorful) {
		position: absolute;
		right: 10px;
		top: 10px;
		width: 20px;
		height: 20px;
		border-radius: 4px;
	}
}
:global(#layer_menu) {
	padding: 20px;
	> ul {		
		padding: 0;
		margin: 0;
		list-style: none;

		:global(.layer_item) {
			display: grid;
			grid-template-columns: 1fr auto;
			height: 40px;
			line-height: 40px;
			cursor: pointer;
			&:global(.selected) {
				color: #0d6efd;
			}		

			> span {
				color: #f00;
			}
		}
	}
}